import React from "react"
import ReactDOM from "react-dom";
import './Components/style.css';


import Mynav from "./Components/navbar";
import Mycard from "./Components/card";



const Mynavbar = () => {
    return (
        <div>
            <Mynav />
        </div>
    )
};

ReactDOM.render(

    <Mynavbar />,
    document.querySelector("#nbar")
)


const MainCont = () => {
    return (
        <div className="container main-cont w-100">
            <div className="row ">
                <div >
                    <Mycard cardTitle="The Godfather"
                        cardText="The Godfather 'Don' Vito Corleone is the head of the Corleone mafia family in New York. He is at the event of his daughter's wedding. Michael, Vito's youngest son and a decorated WW II Marine is also present at the wedding. Michael seems to be uninterested in being a part of the family business."
                        myimg="https://images4.alphacoders.com/217/thumb-1920-217234.jpg"
                        imdb="9.2" />
                </div>
                <div >
                    <Mycard cardTitle="Batman Begins"
                        cardText="When his parents are killed, billionaire playboy Bruce Wayne relocates to Asia, where he is mentored by Henri Ducard and Ra's Al Ghul in how to fight evil. When learning about the plan to wipe out evil in Gotham City by Ducard, Bruce prevents this plan from getting any further and heads back to his home. Back in his original surroundings, Bruce adopts the image of a bat to strike fear into the criminals and the corrupt as the icon known as 'Batman'."
                        myimg="https://wallpaperaccess.com/full/1673042.jpg"
                        imdb="8.2" />
                </div>
                <div >
                    <Mycard cardTitle="Harry Potter and the Sorcerer's Stone"
                        cardText="This is the tale of Harry Potter (Daniel Radcliffe), an ordinary eleven-year-old boy serving as a sort of slave for his aunt and uncle who learns that he is actually a wizard and has been invited to attend the Hogwarts School for Witchcraft and Wizardry. Harry is snatched away from his mundane existence by Rubeus Hagrid (Robbie Coltrane), the groundskeeper for Hogwarts, and quickly thrown into a world completely foreign to both him and the viewer. "
                        myimg="https://vistapointe.net/images/harry-potter-and-the-philosophers-stone-10.jpg"
                        imdb="7.6" />
                </div >
                <div >
                    <Mycard cardTitle="Home Alone 1"
                        cardText="It is Christmas time and the McCallister family is preparing for a vacation in Paris, France. But the youngest in the family, Kevin (Macaulay Culkin), got into a scuffle with his older brother Buzz (Devin Ratray) and was sent to his room, which is on the third floor of his house. Then, the next morning, while the rest of the family was in a rush to make it to the airport on time, they completely forgot about Kevin, who now has the house all to himself."
                        myimg="https://images8.alphacoders.com/803/thumb-1920-803965.jpg"
                        imdb="7.6" />
                </div>
                <div >
                    <Mycard cardTitle="Pulp Fiction"
                        cardText="Jules Winnfield (Samuel L. Jackson) and Vincent Vega (John Travolta) are two hit men who are out to retrieve a suitcase stolen from their employer, mob boss Marsellus Wallace (Ving Rhames). Wallace has also asked Vincent to take his wife Mia (Uma Thurman) out a few days later when Wallace himself will be out of town. Butch Coolidge (Bruce Willis) is an aging boxer who is paid by Wallace to lose his fight. "
                        myimg="https://i.pinimg.com/originals/4d/17/12/4d171266b74b7d851d849b183f4840a4.jpg"
                        imdb="8.9" />
                </div>
            </div>
        </div>
    )
};

ReactDOM.render(
    <MainCont />,
    document.querySelector("#main")

)