import React from "react";

class Mycard extends  React.Component{
    render(){
        return(
            
<div className="card " >
  <div className="row ">
    <div className="col-lg-6">
      <img className="img-fluid img-thumbnail" src={this.props.myimg} alt="..."/>
    </div>
    <div className="col-lg-6">
      <div className="card-body">
        <h5 className="card-title">{this.props.cardTitle}</h5>
        <p className="card-text">{this.props.cardText}</p>
        <p className="card-text"><small className=""></small></p>
    IMDB: <button className="btn btn-dark">{this.props.imdb}</button>
      </div>
    </div>
  </div>
</div>
        )
    }
}


export default Mycard;